* {
    box-sizing: border-box;
}

/* Global font clamp force the value to be between 0.5 and 1*/
:root {
	font-size: clamp(.5rem, calc(1vw + 1vh + .5vmin), 1rem); 
}

body {
    display: grid;
    height: 100vh;
    grid-template-rows: auto 1fr auto;
}

div {
    background-color: rgb(55, 55, 55);
}
  
header {
    padding: 1.5rem;
}

/* main {
    min-width: 90vw;
} */

footer {
    text-align: center;
    margin-top: auto;
    padding: 2vh;
}

body {
    font-family: system-ui, sans-serif;
}

/*Table styles*/
.tg  {
    border-collapse:collapse;border-spacing:0;
}

.timeline {
    margin-top: 2%; 
    margin-bottom: 4%
}


.dataline {
    border-width: 0vw 2px 0vw 0vw !important;
    border-color: white;
    border-style: solid;
    vertical-align: center;
    padding-right: 1vw !important;
    text-align: center;
    width: 12vw;
    max-width: 90px;
}

table {
    width: 70%;
}

td {
  border-width: 0vw;
  overflow: hidden;
  word-break:normal;
}

.tg .tg-0pky{ 
    text-align:left;
    vertical-align:top
}

@media screen and (max-width: 767px) {
    .image {width: 25vw !important; height: 100% !important;}
    .tg {width: auto !important;}
    .tg col {width: auto !important;}
    .tg-wrap {overflow-x: auto;-webkit-overflow-scrolling: touch;}
}